<template>
  <div class="carousel-container">
    <div class="carousel" ref="carousel">
      <div
        class="carousel-item"
        v-for="(review, index) in $tm('reviews')"
        :key="index"
        :class="{'active': activeIndex === index}"
      >
        <div class="review-author">
          <span>{{ review.author }}</span>
        </div>
        <div class="review-content">
          <p>{{ review.text }}</p>
        </div>
      </div>
    </div>
  </div>
</template>

  
  <script>
  export default {
    data() {
      return {
        activeIndex: 0, // ÂΩìÂâçÂç°ÁâáÁ¥¢Âºï
      };
    },
    mounted() {
      if (this.$route.path === '/') {
        this.startAutoScroll();
      }
    },
    methods: {
      startAutoScroll() {
        setInterval(() => {
          if (this.$route.path === '/' && this.$tm ) {
          this.activeIndex = (this.activeIndex + 1) % this.$tm('reviews').length;
          this.$nextTick(() => {
            this.scrollToCurrent();
          });
        }
        }, 3000); // ÊØè3ÁßíÂàáÊç¢‰∏ÄÊ¨°Âç°Áâá
      },
      scrollToCurrent() {
        const container = this.$refs.carousel;
        const activeCard = container.children[this.activeIndex];
        container.scrollTo({
          left: activeCard.offsetLeft, // ÊªöÂä®Âà∞ÂΩìÂâçÂç°Áâá
          behavior: 'smooth'
        });
      }
    }
  };
  </script>
  
