<script>
import axios from 'axios';
import ytTips from '@/assets/yt_tips.png';
export default {
  data() {
    return {
      yt_url: '', // Áî®Êù•Â≠òÂÇ® YouTube ÈìæÊé•
      videoData: {
      },
      loading: false,
      ytTips
    };
  },
  methods: {
    download() {
      alert(`Starting download for: ${this.url}`);
    },
    async downloadFile(link) {
      try {
        // ÂêëÂêéÁ´ØÂèëÈÄÅËØ∑Ê±Ç‰∏ãËΩΩÊñá‰ª∂
        const response = await axios.get(link, {
          responseType: 'blob'  // ËÆæÁΩÆËøîÂõûÁ±ªÂûã‰∏∫‰∫åËøõÂà∂ÊµÅ
        });

        // ÂàõÂª∫‰∏Ä‰∏™‰∏¥Êó∂ÈìæÊé•ÔºåËß¶ÂèëÊµèËßàÂô®‰∏ãËΩΩ
        const blob = response.data;
        const downloadUrl = window.URL.createObjectURL(blob);
        const linkElement = document.createElement('a');
        linkElement.href = downloadUrl;
        linkElement.download = 'video.mp4';  // ÂèØÊ†πÊçÆÈúÄË¶Å‰øÆÊîπÊñá‰ª∂Âêç
        linkElement.click();

        // ÈáäÊîæ URL ÂØπË±°
        window.URL.revokeObjectURL(downloadUrl);
      } catch (error) {
        console.error('‰∏ãËΩΩÂ§±Ë¥•:', error);
        alert('‰∏ãËΩΩÂ§±Ë¥•ÔºåËØ∑Á®çÂêéÂÜçËØï');
      }
    },

    async fetchFormats() {
      if (!this.yt_url) {
        alert('ËØ∑ËæìÂÖ•ÊúâÊïàÁöÑ YouTube ËßÜÈ¢ë URL');
        return;
      }

      this.loading = true;
      this.videoData = {};
      console.log("fetchFormats yt_url: " + this.yt_url);
      try {
        const response = await axios.get('http://127.0.0.1:3000/get-formats', {
          params: { url: this.yt_url }
        });
        this.videoData = response.data; // ‰øùÂ≠òËøîÂõûÁöÑÊ†ºÂºè‰ø°ÊÅØ
        this.loading = false;
        console.log("fetchFormats this.videoData: " + this.videoData);
      } catch (error) {
        console.error('Ëé∑ÂèñÊ†ºÂºèÂ§±Ë¥•:', error);
      }
    },
    // async ytDownlaodTest(){
    //   if (!this.yt_url) {
    //     alert('ËØ∑ËæìÂÖ•ÊúâÊïàÁöÑ YouTube ËßÜÈ¢ë URL');
